$logo-big: 100px;
$logo: 40px;
$big-padding: 100px;
$collapse: 700px;
$sticky: true;

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
  box-shadow: var(--shadow);

  @if $sticky {
    position: sticky !important;
    top: 0;
    z-index: 10 !important;
  }
}

header a {
  color: var(--text);
  text-decoration: none;
}

.home {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}

.title-text {
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
  flex-wrap: wrap;
  gap: 5px;
  min-width: 0;
  font-family: var(--title);
  text-align: left;
}

.title {
  font-size: var(--title);
  line-height: 1.1;
  font-weight: 600;
}

@media (max-width: 768px) {
  header .title {
    text-wrap: balance;
  }
}

.subtitle {
  opacity: 0.65;
  font-weight: var(--thin);
}

/* -------------------------------------------------
   Mobile nav toggle (hamburger) â€” robust on iOS/Safari
-------------------------------------------------- */
.nav-toggle {
  display: none;                 /* hidden on desktop */
  width: 34px;
  height: 34px;
  margin: 0;
  padding: 0;

  appearance: none;
  -webkit-appearance: none;      /* IMPORTANT for mobile Safari */
  border: 0;
  background: transparent;
  cursor: pointer;

  position: relative;
}

.nav-toggle::after {
  content: "\f0c9";              /* hamburger */
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  font-size: 18px;
  line-height: 1;
  color: var(--text);
}

.nav-toggle:checked::after {
  content: "\f00d";              /* X */
}

nav {
  display: flex;
  justify-content: center;
  align-items: end;
  flex-wrap: wrap;
  gap: 10px;
  font-family: var(--heading);
  text-transform: uppercase;
}

nav > a {
  padding: 5px;
}

nav > a:hover {
  color: var(--primary);
}

/* -------------------------------------------------
   Unconditional mobile collapse rules (no data-big)
-------------------------------------------------- */
@media (max-width: $collapse) {
  header {
    justify-content: space-between;
    align-items: center;
  }

  /* show hamburger on mobile */
  .nav-toggle {
    display: block;
    margin-left: auto;
  }

  /* hide nav unless hamburger checked */
  .nav-toggle:not(:checked) + nav {
    display: none !important;
  }

  nav {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 100%;
    gap: 12px;
    padding-top: 10px;
  }
}

/* -------------------------------------------------
   (Optional legacy big-header rules)
   Keep if you might re-enable a big homepage later.
-------------------------------------------------- */
header[data-big] {
  justify-content: space-between;
  align-items: end;
  flex-direction: row;

  @if $sticky {
    top: unset;
  }

  .home {
    flex-direction: row;
    flex-grow: 1;
  }

  .title-text {
    flex-direction: column;
    align-items: start;
    text-align: left;
    text-wrap: wrap;
    margin-top: 100px;
  }

  .title {
    font-size: 4rem;
  }

  .subtitle {
    font-size: 1.5rem;
    text-wrap: wrap;
  }
}

/* -------------------------------------------------
   Active tab underline
-------------------------------------------------- */
header nav a.nav-link {
  position: relative;
  text-decoration: none;
}

header nav a.nav-link[aria-current="page"]::after {
  content: "";
  position: absolute;
  left: 6%;
  right: 6%;
  bottom: 5px;
  height: 1px;
  background: currentColor;
  opacity: 0.95;
  border-radius: 2px;
}
